/* VIDEO CONTENT */
.video-content {
  border-top: 1px solid dimgray;
  margin-bottom: 30px;
}

.title {
  padding: 5px 15px 0;
}

.title {
  letter-spacing: 1px;
  font-weight: bold;
}

.description {
  display: flex;
  flex-flow: row wrap;
  font-size: 12px;
  padding-left: 15px;
  padding-top: 5px;
}

.description p {
  margin-right: 15px;
  color: #9a9fa9;
}

/* NAVBAR LEAGUE */
nav {
  display: flex;
  flex-flow: row wrap;
  padding: 10px 5px;
  position: fixed;
  width: 100%;
  height: auto;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: #fff;
  border-top: 1px solid #9a9fa9;
}

.active-nav {
  font-weight: bold;
}

.content-nav-league {
  display: flex;
  flex-flow: column nowrap;
  flex: 1;
  align-content: center;
  align-items: center;
  text-decoration: none;
}

.img-nav-league {
  width: 100%;
  max-width: 30px;
  height: 30px;
  display: block;
}

.text-nav-league {
  text-align: center;
  font-size: 9px;
  font-weight: lighter;
  color: #000;
  padding-top: 5px;
}

.content-nav-league:hover {
  font-weight: bold;
}

/* LOADING */
.container-loading {
  margin: 0 40vw;
}

.loading {
  border: 1px solid #f3f3f3;
  border-radius: 50%;
  border-top: 1px solid #ea4355;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(0deg);
  }

  50% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
    border-top: 1px solid #ea4355;
  }

  25% {
    border-top: 1px solid #fbbc05;
  }

  50% {
    border-top: 1px solid #34ab53;
  }

  100% {
    transform: rotate(360deg);
    border-top: 1px solid #4285f4;
  }
}

/* CONTENT EMPTY */
.container-empty {
  margin: 20vh 0;
}

.contents-empty img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 100px;
  max-width: 81px;
}

.message-empty {
  text-align: center;
  color: #9a9fa9;
  font-size: 20px;
  letter-spacing: 5px;
  margin-top: 10px;
}

/* END PAGE */
.end-page {
  text-align: center;
  font-weight: lighter;
  color: #9a9fa9;
  margin-bottom: 80px;
}