html,
body {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-weight: 400;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #202020;
  color: #ffffff;
}

* {
  box-sizing: border-box;
}

h1 {
  text-align: left;
  font-size: 1.5em;
  padding: 0 5%;
  margin-bottom: 30px;
}

p {
  margin: 0;
}

hr {
  border: none;
}

/* DESKTOP */
@media only screen and (min-width: 1000px) {
  h1 {
    text-align: center;
  }
}

/* DESKTOP AND IPAD */
@media only screen and (min-width: 680px) {
  h1 {
    font-size: 1.8em;
  }

  hr {
    border-top: 1px solid lightgray;
    margin: 25px;
  }

  .home-fix,
  .fixed-home {
    width: 3em;
    height: 3em;
  }

  .fixed-home {
    margin: 5% 2%;
  }

  .img-nav-league {
    max-width: 40px;
    height: 40px;
  }

  .text-nav-league {
    font-size: 1em;
  }

  .desktop-content-video {
    display: flex;
    flex-flow: row wrap;
    padding-right: 16px;
    padding-left: 16px;
  }

  .video-content {
    margin-top: 0;
    margin-bottom: 25px;
    width: 33.3%;
    padding: 0 10px;
    border: none;
  }

  .title {
    font-size: 1.2em;
    padding: 0;
  }

  .description {
    font-size: 14px;
    padding: 0;
  }

  .end-page {
    margin-top: 20px;
    margin-bottom: 110px;
  }
}
